<template>
  <div>
    <b-row v-if="loading" class="text-center">
      <b-col cols="12">
        <b-spinner class="ml-auto"></b-spinner>
      </b-col>
    </b-row>
    <div v-else>
      <b-row>
        <b-col xl="3">
          <div class="title-fw title-fs m-left-3">{{ $t("EXPENSEYM") }}</div>
        </b-col>
        <b-col>
          <div
            v-if="
              tableDataInfo.cost_monthly_start && tableDataInfo.cost_monthly_end
            "
          >
            {{ getDateMonth(tableDataInfo.cost_monthly_start) }} ～
            {{ getDateMonth(tableDataInfo.cost_monthly_end) }}
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="3">
          <div class="title-fw title-fs m-left-3">
            {{ $t("JOURNALIMPORTFILE") }}
          </div>
        </b-col>
        <b-col>
          <div v-if="tableDataInfo.file_name">
            {{ tableDataInfo.file_name }}
          </div>
        </b-col>
      </b-row>
      <b-row>
        <b-col xl="3">
          <div class="title-fw title-fs ml-5">{{ $t("ROWCOUNT") }}</div>
        </b-col>
        <b-col>
          <div class="mt-1">
            {{ tableData.length }}
          </div>
        </b-col>
      </b-row>
    </div>

    <b-row> </b-row>
    <hr class="hrBarType" />

    <!-- 一覧内容・ボタンリスト -->
    <!-- 一覧内容 -->
    <b-row v-if="loadingTable" class="text-center">
      <b-col cols="12">
        <b-spinner class="ml-auto"></b-spinner>
      </b-col>
    </b-row>
    <ImportTable
      v-else
      :fields="fields"
      :tableData="tableData"
      @itemSelectionClick="showItemSelectModal"
    ></ImportTable>
    <b-row class="d-flex justify-content-end mb-4">
      <b-button
        @click.prevent="updateTheJournalFile()"
        v-if="tableData.length > 0"
        :disabled="linking"
        variant="primary"
        >{{ $t("BINDING") }} <i v-if="linking" class="fa fa-spinner fa-spin"
      /></b-button>
    </b-row>

    <!-- 仕訳帳項目選択 -->
    <b-modal
      id="itemSelect-modal"
      ref="itemSelect-modal"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="sm"
      footer-class="border-0"
      header-class="border-0 bg-white"
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fw title-fs text-dark">
          {{ $t("JOURNALIMPORTITEMSELECTION") }}
        </div>
        <div class="d-flex justify-content-end">
          <b-button
            @click="cancel()"
            variant="primary"
            class="back-btn close-btn mt-2"
          >
            {{ $t("CLOSE") }}
          </b-button>
        </div>
      </template>

      <b-card class="pop-up-box">
        <div class="my-0 py-0 px-2">
          <b-form-radio-group v-model="radioSelectOptions" stacked>
            <b-radio class="highLightHeaderLabel" value="0">{{
              $t("TRADINGDAY")
            }}</b-radio>
            <b-radio class="highLightHeaderLabel" value="1">{{
              $t("ACCOUNT")
            }}</b-radio>
            <b-radio class="highLightHeaderLabel" value="2">{{
              $t("AMOUNT")
            }}</b-radio>
            <b-radio class="highLightHeaderLabel" value="3">{{
              $t("DESCRIPTION")
            }}</b-radio>
          </b-form-radio-group>
        </div>
      </b-card>

      <template v-slot:modal-footer="">
        <div class="w-75 d-flex justify-content-end">
          <button @click="select()" class="btn-sm px-1 float-left btn-primary">
            {{ $t("SELECTEXPENSEITEMS") }}
          </button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import { Settings } from "luxon";
import { required } from "vuelidate/lib/validators";
Settings.defaultLocale = "ja";
import api from "@/shared/api";
import ImportTable from "@/components/tables/importTable";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "ItemConfirmation",
  components: { ImportTable },
  data() {
    return {
      tableData: [],
      loading: false,
      loadingTable: false,
      tableDataInfo: [],
      tableBtnData: [],
      itemIndex: null,
      radioSelectOptions: null,
      rowCount: "",
      fields: [],
      file_no: "",
      mappingOptions: {
        deal_date: "",
        accounts_name_nm: "",
        cost: "",
        description: "",
      },
      linking: false,
    };
  },

  validations: {
    mappingOptions: {
      deal_date: {
        required,
      },
      accounts_name_nm: {
        required,
      },
      cost: {
        required,
      },
      description: {
        required,
      },
    },
  },
  computed: {
    /**
     * @description mapGetters to get the data from the store
     */
    ...mapGetters({
      user_details: "getUserDetails",
      journal_import_data: "getJournalImportData",
    }),
  },
  mounted() {
    this.file_no = this.journal_import_data.id; // get the journal file id from store
    this.rowCount = this.journal_import_data.rowCount
      ? this.journal_import_data.rowCount
      : "0";
    this.getFileInfo();
  },
  methods: {
    /**
     * @description function get the year and month from a given number
     * @name getDateMonth
     * @param dateInput
     * @returns string 'formatted date string'
     */
    getDateMonth(dateInput) {
      if (!dateInput) {
        return "";
      }
      return (
        String(dateInput).slice(0, 4) +
        this.$t("YEAR") +
        String(dateInput).slice(4, 6) +
        this.$t("MONTH")
      );
    },
    /**
     * @description function to map the columns in the table
     * @name select
     * @param none
     * @returns nothing
     */
    select() {
      // based on column selection
      switch (this.radioSelectOptions) {
        case "0": {
          let filter = this._.filter(this.fields, {
            index: this.mappingOptions.deal_date,
          });
          if (filter.length > 0) {
            filter[0].index = null;
            filter[0].label = "";
          }
          this.mappingOptions.deal_date = this.itemIndex;
          this.fields[this.itemIndex].label = this.$t("TRADINGDAY");
          this.fields[this.itemIndex].index = this.itemIndex;
          break;
        }
        case "1": {
          let filter = this._.filter(this.fields, {
            index: this.mappingOptions.accounts_name_nm,
          });
          if (filter.length > 0) {
            filter[0].index = null;
            filter[0].label = "";
          }
          this.mappingOptions.accounts_name_nm = this.itemIndex;

          this.fields[this.itemIndex].label = this.$t("ACCOUNT");
          this.fields[this.itemIndex].index = this.itemIndex;
          break;
        }
        case "2": {
          let filter = this._.filter(this.fields, {
            index: this.mappingOptions.cost,
          });

          if (filter.length > 0) {
            filter[0].index = null;
            filter[0].label = "";
          }
          this.mappingOptions.cost = this.itemIndex;
          this.fields[this.itemIndex].label = this.$t("AMOUNT");
          this.fields[this.itemIndex].index = this.itemIndex;
          break;
        }
        case "3": {
          let filter = this._.filter(this.fields, {
            index: this.mappingOptions.description,
          });

          if (filter.length > 0) {
            filter[0].index = null;
            filter[0].label = "";
          }
          this.mappingOptions.description = this.itemIndex;
          this.fields[this.itemIndex].label = this.$t("DESCRIPTION");
          this.fields[this.itemIndex].index = this.itemIndex;

          break;
        }
      }
      this.updateMappingOptions(this.radioSelectOptions, this.itemIndex);
      this.$refs["itemSelect-modal"].hide();
    },
    /**
     * @description function to clear the previous column selection
     * @name updateMappingOptions
     * @param radioSelection
     * @param itemIndex
     * @returns nothing
     */
    updateMappingOptions(radioSelection, itemIndex) {
      // '0' => deal_date, '1'=> accounts_name_nm, '2'=> cost, '3'=> description
      switch (radioSelection) {
        case "0":
          {
            if (this.mappingOptions.description == itemIndex) {
              this.mappingOptions.description = null;
            } else if (this.mappingOptions.cost == itemIndex) {
              this.mappingOptions.cost = null;
            } else if (this.mappingOptions.accounts_name_nm == itemIndex) {
              this.mappingOptions.accounts_name_nm = null;
            }
          }
          break;
        case "1":
          {
            if (this.mappingOptions.description == itemIndex) {
              this.mappingOptions.description = null;
            } else if (this.mappingOptions.cost == itemIndex) {
              this.mappingOptions.cost = null;
            } else if (this.mappingOptions.deal_date == itemIndex) {
              this.mappingOptions.deal_date = null;
            }
          }
          break;
        case "2":
          {
            if (this.mappingOptions.description == itemIndex) {
              this.mappingOptions.description = null;
            } else if (this.mappingOptions.accounts_name_nm == itemIndex) {
              this.mappingOptions.accounts_name_nm = null;
            } else if (this.mappingOptions.deal_date == itemIndex) {
              this.mappingOptions.deal_date = null;
            }
          }
          break;
        case "3":
          {
            if (this.mappingOptions.cost == itemIndex) {
              this.mappingOptions.cost = null;
            } else if (this.mappingOptions.accounts_name_nm == itemIndex) {
              this.mappingOptions.accounts_name_nm = null;
            } else if (this.mappingOptions.deal_date == itemIndex) {
              this.mappingOptions.deal_date = null;
            }
          }
          break;
      }
    },
    /**
     * @description function to update the mapped columns to db
     * @name updateTheJournalFile
     * @param none
     * @returns nothing
     */
    async updateTheJournalFile() {
      this.$v.mappingOptions.$touch();
      if (!this.$v.mappingOptions.$invalid) {
        this.linking = true;
        const { data } = await api.post("journal/journalfileconfirm", {
          file_no: this.file_no,
          column_array: {
            deal_date: this.mappingOptions.deal_date + 1,
            description: this.mappingOptions.description + 1,
            accounts_name_nm: this.mappingOptions.accounts_name_nm + 1,
            cost: this.mappingOptions.cost + 1,
          },
          create_id: this.user_details.client_account_no,
          update_id: this.user_details.client_account_no,
          file_path: this.tableDataInfo.file_path,
        });

        if (data.success) {
          this.$swal({
            type: "success",
            text: "",
            timer: 5000,
          });
        } else {
          this.$swal({
            type: "warning",
            text: this.$t("ERRORWHILELINKING"),
            timer: 5000,
          });
        }
      } else {
        this.$swal({
          type: "warning",
          text: this.$t("ERRORWHILELINKING"),
          timer: 5000,
        });
      }
      this.linking = false;
    },
    /**
     * @description function to get the journal file informaton based on the file_no
     * @name getFileInfo
     * @param none
     * @return nothing
     */
    async getFileInfo() {
      this.loading = true;
      const { data } = await api.post(endPoint.MASTERREADTJOURNALFILEINFO, {
        fieldlist: ["*"],
        conditions: [
          {
            key: "file_no",
            value: this.file_no,
          },
        ],
      });
      if (data.success) {
        this.loading = false;
        this.tableDataInfo = data.data[0];
        this.getTableData();
      } else {
        this.loading = false;
      }
    },
    /**
     * @description function to get the file data by file_no
     * @name getTableData
     * @param none
     * @return nothing
     */
    async getTableData() {
      this.loadingTable = true;
      const { data } = await api.post(endPoint.MASTERREADTJOURNALDATAINFO, {
        fieldlist: ["*"],
        conditions: [
          {
            key: "file_no",
            value: this.file_no,
          },
        ],
      });

      if (data.success) {
        this.loadingTable = false;
        let responseData = data.data;

        //construct the dynamic fields
        if (responseData.length > 0) {
          let fieldItems = responseData[0].journal_data.split(",");
          fieldItems.forEach((item, index) => {
            this.fields.push({
              key: `Item${index + 1}`,
              label: "",
              index: null,
              class: "text-left normal-width",
            });
          });

          // if columns already mapped then substract 1 from columns no, because index starts from 0
          this.mappingOptions.deal_date =
            this.tableDataInfo.deal_dt_column_no - 1;
          this.mappingOptions.description =
            this.tableDataInfo.description_column_no - 1;
          this.mappingOptions.cost = this.tableDataInfo.cost_column_no - 1;
          this.mappingOptions.accounts_name_nm =
            this.tableDataInfo.accounts_name_column_no - 1;

          // assign the labels to the fileds to dispaly
          this.fields[this.mappingOptions.deal_date].label = this.$t(
            "TRADINGDAY"
          );
          this.fields[
            this.mappingOptions.deal_date
          ].index = this.mappingOptions.deal_date;
          this.fields[this.mappingOptions.description].label = this.$t(
            "DESCRIPTION"
          );
          this.fields[
            this.mappingOptions.description
          ].index = this.mappingOptions.description;

          this.fields[this.mappingOptions.accounts_name_nm].label = this.$t(
            "ACCOUNT"
          );
          this.fields[
            this.mappingOptions.accounts_name_nm
          ].index = this.mappingOptions.accounts_name_nm;

          this.fields[this.mappingOptions.cost].label = this.$t("AMOUNT");
          this.fields[
            this.mappingOptions.cost
          ].index = this.mappingOptions.cost;
        }
        //iterate the respose data of jounal file to construct the data to display in the table format
        if (responseData.length > 0) {
          responseData.forEach((item) => {
            let row = {};
            let journalData = item.journal_data.split(",");

            journalData.forEach((elm, index) => {
              row[`Item${index + 1}`] = elm;
            });
            this.tableData.push(row);
          });
          this.tableData.forEach((element, index) => {
            if (index === this.tableData.length - 1) {
              this.tableData[index].last_element = true;
              return;
            }
          });
        }
        if (responseData.length == 0) {
          this.$swal({
            type: "warning",
            text: this.$t("NODATAFOUNDINFILE"),
          });
        }
      } else {
        this.loadingTable = false;
      }
    },
    /**
     * @description function to show column mapping item selection modal
     * @name showItemSelectModal
     * @param index
     * @return noting
     */
    showItemSelectModal(index) {
      this.itemIndex = index;
      this.$refs["itemSelect-modal"].show();
    },
  },
};
</script>

<style>
.hrBarType {
  height: 2px;
  border-width: 10;
  color: gray;
  background-color: gray;
}

.btn-min-width {
  min-width: 70px;
}
</style>
